<template>
  <div style="display:flex;flex-direction:column;width:100%;position:relative;flex:1">
    <page-header @btn-action="openApp"/>
      <div class="flex-full" ref="quality" v-if="qualityDetail.shareData">
        <section class="quality-main-image">
          <div>
            <van-image :src="qualityDetail.shareData.picUrl" width="100%">
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
            </van-image>
          </div>
        </section>
        <section class="quality-detail">
          <div class="container-top">
            <p>{{qualityDetail.shareData.title}}</p>
            <div class="message">
              <span>{{qualityDetail.shareData.publishTime}}</span>
              <span>阅读{{qualityDetail.shareData.readTimes}}</span>
            </div>
          </div>
          <div class="container-bottom">
            <div v-for="(item,index) in qualityDetail.goodsIdArr" :key="index">
              <div v-html="item.htmlStr"></div>
              <div class="goods-item" v-if="item.spuCode">
                <div class="left">
                  <img :src="item.spuPicUrl" alt />
                </div>
                <div class="center">
                  <p class="title">{{item.spuName}}</p>
                  <p class="desc">{{item.spuSalePoint}}</p>
                  <p class="price">￥{{item.skuPrice}}</p>
                </div>
                <div class="right">
                  <!-- <div class="goods-btn" @click="payGoods(item.spuCode)">立即购买</div> -->
                  <div class="goods-btn" @click="openApp">立即购买</div>
                </div>
              </div>
              <!-- 底部查看更多 -->
              <div class="showMore" v-if="showMore==true">
                <button @click="openApp">更多精彩，立即查看</button>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
</template>

<script>
// import { mapActions, mapState } from "vuex";
import { ImagePreview, Toast, Dialog } from "vant";
import { getShareArticle, getArticleGoods } from "../../service/shareService";
import PageHeader from "../../components/WechatShare/PageHeader"

// import Vue from "vue";
export default {
  name: "QualityDetailPage",
  components: {
    PageHeader
  },
  created() {},
  props: {
    activeIndex: {
      type: Number
    }
  },
  data() {
    return {
      fullBar: true,
      fullCollectIcon: "",
      normalCollectIcon: "",
      qualityTitle: "品质生活标题",
      qualityDetail: {},
      hasCollection: "",
      contentShow: false,
      showMore: false, //点击更多精彩状态
      img: {
        xwjlogo: require("../../assets/share/shareLogo.png")
      },
      articleNo: ""
    };
  },

  mounted() {
    this.getArticleDetail();
    window.addEventListener("scroll", this.handleScroll, true);
    console.log("通过url获取到的参数：" + this.$route.params.id);
  },
  methods: {
    /**
     * 获取品质生活文章详情
     *
     **/
    getArticleDetail() {
      this.getQualityDetailAction(this.$route.params.id)
        .then(data => {
          console.log("小区列表：" + data.areaCode);
          this.getGoodsInfo(data.goodsIdArr, data.areaCode);
          this.qualityDetail = data;
          document.title = this.qualityDetail.shareData.title;
          console.log("请求回来的data:" + this.qualityDetail);
        })
        .catch(error => {
          console.log(error);
          Toast.fail(error);
        });
    },
    //品质生活详情
    getQualityDetailAction(id) {
      return new Promise((resolve, reject) => {
        const data = {
          id: this.$route.params.id.split("=")[1]
        };
        getShareArticle(data).then(
          article => {
            if (article.resultCode == "0") {
              //请求品质生活的状态码，0代表请求成功
              this.articleNo = article.data.shareData.articleNo;
              let urlString = "qualityLife?articleNo=" + this.articleNo;
              this.androidShareOpenApp(urlString); //android 拉起APP
              let content = article.data.shareData.content;
              let b = article.data.shareData.content;
              let goodsIdArr = content.match(/GOOD_ID=[a-z0-9]+/gi);
              let splitArr = b.split(/(GOOD_ID=[a-z0-9]+)/);
              if (goodsIdArr && goodsIdArr.length) {
                //如果文章内存在商品
                let matcher = [];
                goodsIdArr.forEach((goods, index) => {
                  matcher.push({
                    goodsId: goods.replace(/GOOD_ID=/, ""),
                    htmlStr: splitArr[index * 2]
                  });
                });
                if (splitArr.length > goodsIdArr.length) {
                  //分割出商品信息后补充最后一段富文本
                  matcher.push({
                    htmlStr: splitArr[goodsIdArr.length * 2]
                  });
                }
                article.data.goodsIdArr = matcher;
              } else {
                //文章内不存在商品时
                article.data.goodsIdArr = [
                  {
                    htmlStr: article.data.shareData.content
                  }
                ];
              }
              resolve(article.data);
            } else {
              Dialog.alert({
                message: article.msg
              }).then(() => {});
            }
          },
          fail => {
            reject(fail);
          }
        );
      });
    },

    /**
     * 监听滚动条事件
     *
     **/
    handleScroll() {
      let scrollTop = this.$refs.quality.scrollTop;
      let h = document.getElementsByClassName("flex-full")[0].clientHeight; //可见宽度

      console.log(scrollTop, h);
      if (scrollTop >= this.$refs.quality.scrollHeight - h) {
        this.showMore = true;
      } else {
        this.showMore = false;
      }
    },

    /**
     * 获取富文本中包含的商品信息
     * @param
     * goodsData   Array<Object>  当前富文本组合对象
     *小区编码
     **/
    getGoodsInfo(goodsData, areaCode) {
      goodsData.forEach((goods, index) => {
        if (goods.goodsId) {
          const data = {
            areaCode: areaCode, //小区编码
            spuCodeList: goodsData[index].goodsId
          };
          getArticleGoods(data)
            .then(res => {
              if (res.resultCode == "0" && res.data && res.data.length) {
                let newGoodsData = Object.assign(goodsData[index], res.data[0]);
                this.$set(this.qualityDetail.goodsIdArr, index, newGoodsData);
              }
            })
            .catch(error => {
              Toast.fail(error);
            });
        }
      });
    },

    /**
     * 跳转至商品页面进行购买
     * @param
     * skuNo     String      商品编码
     *
     **/
    payGoods(spuCode) {
      this.$router.push({
        path: "/shareGoods/id=" + spuCode
      });
    },
    openApp() {
      let urlString = "qualityLife?articleNo=" + this.articleNo;
      if (!this.articleNo) {
        urlString = "";
      }
      this.shareOpenApp(urlString);
    }
  }
};
</script>

