//获取秒杀商品秒杀信息
const getShareFlashSaleGoods=(params) => {
    console.log(params)
    return new Promise((resolve, reject) => {
        http.get('xwj-sys-user/share/seckillDetail', params).then(result => {
            result.resultCode == "0" ? resolve(result) : reject(result.msg);
        }).catch(error => {
            reject(error);
        })
    });
}
//获取秒杀商品的详情信息
