// 获取商品详情内的店铺信息
const getProductShopInfo = (reqParam) => {
    return new Promise((resolve, reject) => {
        http.get('xwj-commerce-shop/shopManage/getOneShopTitle',reqParam,'baseUrl',false).then(result => {
             resolve(result);
        }).catch(error => {
            reject(error);
        })
    });
}