<script>
    import { getShareActivityDetail,getShareActivity } from "../../service/shareService";
    import {Toast} from "vant";

export default {
    data(){
        return{
            setVantHeight: {},  //设置为你推荐的高度
            goodsListData: [],//商品集合
            sourceList:{},//模板信息
            shareId:0,
            advertId:0,
            showMore:false,//底部显示更多
            img:{
                shoppingCart:require("@/assets/img/superiorProducts/navbar_icon_shoopingCart_black.png"),
                share:require("@/assets/img/superiorProducts/navbar_icon_share_black.png"),
                xwjlogo:require("../../assets/share/xinwojiaLogo36@2x.png"),
                addToCart:require("../../assets/shopCart/order_icon_subtract_add_click.png")
            }
        }
    },
    methods:{

         goToBack(){
          this.$router.go(-1);//返回上一层
        },
        //获取商品活动页商品信息
        getActivityLists(){
        const data={
          id:this.$route.params.id.split("=")[1]
        }
        console.log("活动模板");
        console.log("data:"+data);
        getShareActivity(data).then(Response=>{
          this.goodsListData=Response.data.shareData;
          this.advertId=Response.data.advertId;
          this.getActivityTepmlate(Response.data.advertId);
        })
      },
        //获取活动模板信息
        getActivityTepmlate(advertId){
          const data={
            id:advertId,
          }
        getShareActivityDetail(data).then(Response=>{
              this.sourceList=Response.data;
          });
        },
      openApp(){
        const urlString="activityTemplate?templateId="+this.advertId;
        this.shareOpenApp(urlString);
      },
      handleScroll: function () {
        const _this=this; 
        const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
            const h =window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight; //可见宽度
            if( scrollTop >= document.documentElement.scrollHeight -h ) { 
               _this.showMore=true;
            }else{
               _this.showMore=false;
            }
      },
  },
  mounted:function(){
    this.getActivityLists();
     window.addEventListener("scroll", this.handleScroll, true);
  },
}

</script>